.App {
  text-align: center;
}
img {
  box-shadow: rgba(47, 9, 236, 0.2) 6px 6px;
  display: flex;
  justify-content: space-around;
  align-content: space-evenly;
  align-items: center;
  /* padding-bottom: 30px; */

  width: auto;
  max-width: calc(100% - 50px);
  height: auto;
  margin: 20px 20px 20px 20px;

  /* width: 500px;
  height: auto; */
}

body {
  background-image: url("./pages/2.jpg");
  padding: 0%;
  margin: 0%;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  font-family: "Times New Roman", "serif", monospace;
}
h3 {
  display: flex;
  justify-content: right;
  font-family: "Lucida Handwriting", "Courier New", monospace;
  font-size: 25px;
  font-weight: 400;
}
.wrap {
  width: 1200px;
}
video {
  display: block;
  width: 100%;
  align-content: space-around;
}

nav {
  font-family: "Georgia", "Courier New", monospace;
  margin: 0;
  width: 100%;
  height: 80px;
  background-color: rgb(53, 114, 213);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-decoration: none;
}

a {
  color: white;
  text-decoration: none;
  margin: 10px;
  font-size: 25px;
}

.createPostPage {
  width: 100%;
  height: calc(100vh - 80px);
  display: grid;
  place-items: center;
}

.cpContainer {
  width: 500px;
  height: auto;
  padding: 20px;
  background-color: rgba(16, 121, 225, 0.515);
  border-radius: 12px;
  color: rgb(243, 8, 110);
  display: flex;
  flex-direction: column;

  font-family: "Georgia", "Courier New", monospace;
}

.cpContainer h1 {
  text-align: center;
}

.cpContainer label {
  font-size: 25px;
}
.cpContainer .inputGp {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.inputGp input,
.inputGp textarea {
  font-size: 18px;
  border: none;
  border-radius: 2px;
  padding: 5px;
}

.inputGp input {
  height: 40px;
}
.inputGp textarea {
  height: 150px;
}

.cpContainer button {
  margin-top: 20px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}
input,
textarea {
  margin-top: 5px;
}

.loginPage {
  width: 100vw;
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loginPage p {
  font-size: 30px;
}

/* GOOGLE BUTTOn */
.login-with-google-btn {
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
  padding: 5px 16px 12px 42px;
  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  color: #757575;
  font-size: 25px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;
}
.login-with-google-btn:hover {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25);
}
.login-with-google-btn:active {
  background-color: #eeeeee;
}
.login-with-google-btn:focus {
  outline: none;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25),
    0 0 0 3px #c8dafc;
}
.login-with-google-btn:disabled {
  filter: grayscale(100%);
  background-color: #ebebeb;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  cursor: not-allowed;
}

.homePage {
  width: 100%;
  /* min-height: calc(100vh - 80px); */
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}

.homePage .post {
  width: 1000px;
  height: auto;
  max-height: max-content;
  background-color: rgba(65, 200, 87, 0.377);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin: 20px;
  padding: 20px;
  border-radius: 15px;
}

.post .postHeader {
  display: flex;
  justify-content: center;
  width: 100%;
}

.postHeader .title {
  flex: 50%;
}
.postHeader .deletePost {
  flex: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.uploaded {
  font-family: "Georgia", "serif", monospace;
  display: flex;

  align-content: flex-end;
  border-style: groove;
  border-radius: 5%;
  border-width: 5px;
  border-color: rgb(4, 247, 21);
  color: rgb(255, 255, 255);
  background: #209f39;
  width: fit-content;
  font-size: 20px;
  cursor: pointer;
  font-weight: bolder;
}
.deletePost button {
  border-style: groove;
  border-radius: 5%;
  border-width: 3px;
  border-color: red;
  color: red;
  background: none;
  font-size: 20px;
  cursor: pointer;
  font-weight: bolder;
}
.post .add {
  font-size: 10pxpx;
}

.post .postTextContainer {
  scroll-padding-top: 30px;
  margin-top: 20px;
  word-wrap: break-word;
  font-family: "Georgia", "serif", monospace;
  font-size: 20px;
  height: auto;
  padding-top: 30px;
  padding-bottom: 20px;
  border-width: 30px;
  max-height: 400px;
  width: 100%;

  overflow: hidden;
  overflow-y: auto;
}
.logout {
  font-family: "Georgia", "Courier New", monospace;
  margin: 0;
  font-size: 25px;

  background-color: rgb(53, 114, 213);
  color: rgb(255, 255, 255);
}
